import {
	Button,
	Checkbox,
	IconButton,
	InputBase,
	List,
	ListItem,
	ListItemButton,
	ListItemIcon,
	ListItemText,
	Modal,
	Paper,
	Typography,
} from '@mui/material';
import { Box } from '@mui/system';
import { useState } from 'react';
import SearchIcon from '@mui/icons-material/Search';
import { debounce } from 'throttle-debounce';
export default function ModalAddVisiter({
	event,
	users,
	isOpenAddVisiter,
	handleCloseAddVisiter,
	handleAddVisiter,
}) {
	const [checked, setChecked] = useState(
		event.listVisitersCheckin.reduce((prevUser, curUser) => {
			prevUser.push(curUser.visiter);
			return prevUser;
		}, []),
	);

	const [searchFillter, setSearchFillter] = useState('');
	const handleFillter = (e) => setSearchFillter(e.target.value);
	const handleFillterDebounce = debounce(500, handleFillter);
	const handleToggle = (value) => () => {
		const currentIndex = checked.indexOf(value);
		const newChecked = [...checked];

		if (currentIndex === -1) {
			newChecked.push(value);
		} else {
			newChecked.splice(currentIndex, 1);
		}

		setChecked(newChecked);
	};
	return (
		<Modal
			open={isOpenAddVisiter}
			aria-labelledby="modal-modal-title"
			aria-describedby="modal-modal-description"
		>
			<Box
				sx={{
					position: 'absolute',
					top: '50%',
					left: '50%',
					transform: 'translate(-50%, -50%)',
					width: 600,
					height: 'calc(100vh - 300px)',
					bgcolor: 'white',
					boxShadow: 24,
				}}
			>
				<Box
					sx={{
						bgcolor: 'blue',
						color: 'white',
						p: 3,
					}}
				>
					<Typography
						id="modal-modal-title"
						variant="h5"
						component="h2"
						align="center"
						sx={{ fontWeight: 'bolder' }}
					>
						Thm Ngi Tham D
					</Typography>
				</Box>
				<Box
					sx={{
						p: 3,
					}}
				>
					<Paper
						component="form"
						sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: '100%' }}
					>
						<InputBase
							sx={{ ml: 1, flex: 1 }}
							placeholder="Tm kim ngi dng"
							inputProps={{ 'aria-label': 'Tm kim ngi dng' }}
							onChange={handleFillterDebounce}
						/>
						<IconButton type="button" sx={{ p: '10px' }} aria-label="search">
							<SearchIcon />
						</IconButton>
					</Paper>
					<List
						sx={{
							width: '100%',
							height: 'calc(100vh - 550px)',
							overflow: 'scroll',
							marginTop: 2,
						}}
					>
						{users
							.filter((user) => user.username.match(searchFillter))
							.map((user) => {
								const labelId = `checkbox-list-label-${user._id}`;
								return (
									<ListItem key={user._id} disablePadding>
										<ListItemButton
											role={undefined}
											onClick={handleToggle(user._id)}
											dense
										>
											<ListItemIcon>
												<Checkbox
													edge="start"
													checked={checked.indexOf(user._id) !== -1}
													tabIndex={-1}
													disableRipple
													inputProps={{ 'aria-labelledby': labelId }}
												/>
											</ListItemIcon>
											<ListItemText
												id={labelId}
												primary={
													<>
														<Typography>{user.username}</Typography>
														<Typography>
															<b style={{ color: '#0000FF' }}>
																H v tn:&nbsp;
															</b>
															{user.fullName}
														</Typography>
														<Typography>
															<b style={{ color: '#2e7d32' }}>
																a ch email:&nbsp;
															</b>
															{user.email}
														</Typography>
													</>
												}
											/>
										</ListItemButton>
									</ListItem>
								);
							})}
					</List>
					<Box
						sx={{
							p: 3,
							display: 'flex',
							justifyContent: 'flex-end',
						}}
					>
						<Button
							variant="contained"
							color="success"
							type="button"
							onClick={() => {
								handleCloseAddVisiter();
								handleAddVisiter(event, checked);
							}}
						>
							Thm
						</Button>

						<Button
							variant="contained"
							color="error"
							onClick={handleCloseAddVisiter}
							sx={{ ml: 3 }}
						>
							Hy
						</Button>
					</Box>
				</Box>
			</Box>
		</Modal>
	);
}
