import { Button, Container, Grid, Link, TextField, Typography } from '@mui/material';
import { Box } from '@mui/system';
import Logo from '../../assets/images/logoSmall.png';
import { useForm } from 'react-hook-form';
import { signUp } from '../../api/authApi';
import { useState, useEffect } from 'react';
function SignUp() {
	const {
		register,
		handleSubmit,
		setError,
		formState: { errors },
	} = useForm();
	const [err, setErr] = useState('');
	useEffect(() => (document.title = 'ng k'), []);

	const submitSignUp = async (data) => {
		try {
			const { confirmPassword, ...signUpData } = data;
			if (confirmPassword !== signUpData.password) setError('confirmPassword');
			else {
				const token = await signUp(signUpData);
				if (token) {
					localStorage.setItem('token', token.token);
					window.location = '/event';
				}
			}
		} catch (error) {
			if (error.status === 400 && error.data.message.startsWith('Duplicate fields')) {
				let errStr = error.data.message
					.split(',')[0]
					.replace('Duplicate fields: ', ' c ngi s dng ');
				errStr += ' ny!';
				setErr(errStr);
			}
		}
	};
	return (
		<Container component="main" maxWidth="xs">
			<Box
				sx={{
					marginTop: 8,
					display: 'flex',
					flexDirection: 'column',
					alignItems: 'center',
				}}
			>
				<img
					src={Logo}
					alt={'Logo'}
					style={{
						height: '100px',
						width: '100px',
						borderRadius: '50%',
						marginBottom: '30px',
					}}
				/>
				<Typography component="h1" variant="h5">
					ng k
				</Typography>
				<Typography component="h6" color="red" my={2}>
					{err ? err : ''}
				</Typography>
				<Box
					component="form"
					noValidate
					onSubmit={handleSubmit(submitSignUp)}
					sx={{ mt: 3 }}
				>
					<Grid container spacing={2}>
						<Grid item xs={12}>
							<TextField
								required
								fullWidth
								id="username"
								label="Username"
								name="username"
								{...register('username', {
									required: true,
									minLength: 4,
									maxLength: 20,
								})}
								error={errors.username ? true : false}
								helperText={
									errors.username &&
									'Username khng c  trng v c t nht 4 k t'
								}
							/>
						</Grid>
						<Grid item xs={12}>
							<TextField
								required
								fullWidth
								name="password"
								label="Password"
								type="password"
								id="password"
								{...register('password', {
									required: true,
									minLength: 8,
									maxLength: 32,
								})}
								error={errors.password ? true : false}
								helperText={
									errors.password &&
									'Password khng c  trng v c t nht 8 k t'
								}
							/>
						</Grid>
						<Grid item xs={12}>
							<TextField
								required
								fullWidth
								name="confirmPassword"
								label="Nhp li password"
								type="password"
								id="confirmPassword"
								{...register('confirmPassword', {
									required: true,
									minLength: 8,
									maxLength: 32,
								})}
								error={errors.confirmPassword ? true : false}
								helperText={
									errors.confirmPassword &&
									'Password khng chnh xc vui lng nhp li'
								}
							/>
						</Grid>
						<Grid item xs={12}>
							<TextField
								type="email"
								required
								fullWidth
								id="email"
								label="a ch email"
								name="email"
								{...register('email', { required: true })}
								error={errors.email ? true : false}
								helperText={errors.email && 'Email khng c  trng'}
							/>
						</Grid>
						<Grid item xs={12}>
							<TextField
								required
								fullWidth
								id="fullName"
								label="H v tn"
								name="fullName"
								{...register('fullName', { required: true })}
								error={errors.fullName ? true : false}
								helperText={errors.fullName && 'H v tn khng c  trng'}
							/>
						</Grid>
						<Grid item xs={12}>
							<TextField
								fullWidth
								id="phone"
								label="S in thoi"
								name="phone"
								{...register('phone')}
							/>
						</Grid>
						<Grid item xs={12}>
							<TextField
								fullWidth
								id="address"
								label="a Ch"
								name="address"
								{...register('address')}
							/>
						</Grid>
						<Grid item xs={12}>
							<TextField
								fullWidth
								id="workUnit"
								label="n v cng tc"
								name="workUnit"
								{...register('workUnit')}
							/>
						</Grid>
						<Grid item xs={12}>
							<TextField
								fullWidth
								id="addressUnit"
								label="a ch n v cng tc"
								name="addressUnit"
								{...register('addressUnit')}
							/>
						</Grid>
						<Grid item xs={5}>
							<TextField
								fullWidth
								id="idCB"
								name="idCB"
								label="M s cn b"
								{...register('idCB')}
							/>
						</Grid>
						<Grid
							item
							xs={2}
							textAlign="center"
							justifyContent="center"
							flexDirection="column"
						>
							<Typography variant="body2">Hoc</Typography>
						</Grid>
						<Grid item xs={5}>
							<TextField
								fullWidth
								id="idSV"
								name="idSV"
								label="M s sinh vin"
								{...register('idSV')}
							/>
						</Grid>
					</Grid>
					<Button type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
						ng k
					</Button>
					<Grid container justifyContent="flex-end">
						<Grid item>
							<Link href="/" variant="body2">
								Bn  c ti khon? ng nhp ti y.
							</Link>
						</Grid>
					</Grid>
				</Box>
			</Box>
		</Container>
	);
}
export default SignUp;
