import {
	Button,
	Card,
	CardContent,
	Container,
	Grid,
	Paper,
	TextField,
	Toolbar,
	Typography,
} from '@mui/material';
import { Box } from '@mui/system';
import { useEffect, useState } from 'react';
import { addVisiters, getOwnerEvent } from '../../../../api/eventApi';
import { deleteEvent, updateEvent } from '../../../../api/eventApi';
import { getAllVisisters } from '../../../../api/userApi';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DatePicker from '@mui/lab/DatePicker';
import RowEvent from './RowEvent';
import enLocale from 'date-fns/locale/en-US';
import ModalAddEvent from './ModalAddEvent';
import Swal from 'sweetalert2';
function EventManagerComponent({ children }) {
	const [page, setPage] = useState(0);
	const [rowsPerPage, setRowsPerPage] = useState(10);
	const [date, setDate] = useState(null);
	const [events, setEvents] = useState([]);
	const [eventsFilter, setEventsFilter] = useState([]);
	const [modalAddEvent, setModalAddEvent] = useState(false);
	const [listUsers, setListUsers] = useState([]);
	const handleOpenModalAddEvent = () => setModalAddEvent(true);
	const handleCloseModalAddEvent = () => setModalAddEvent(false);
	useEffect(() => {
		document.title = 'Qun l s kin';
		getOwnerEvent().then((res) => setEvents(res.event));
		getAllVisisters().then((res) => setListUsers(res.users));
		return () => {
			setListUsers([]);
			setEvents([]);
			setEventsFilter([]);
		};
	}, []);
	useEffect(() => {
		if (date === null) setEventsFilter(events);
		else
			setEventsFilter(
				events.filter(
					(event) =>
						new Date(event.dateEvent).toDateString() === new Date(date).toDateString(),
				),
			);
	}, [date, events]);

	const handleChangePage = (event, newPage) => {
		setPage(newPage);
	};

	const handleChangeRowsPerPage = (event) => {
		setRowsPerPage(+event.target.value);
		setPage(0);
	};

	const addNewEventState = (event) => {
		setEvents([...events, event]);
	};
	const handleUpdateEvent = (eventUpdate, dataUpdate) => {
		updateEvent(eventUpdate._id, dataUpdate)
			.then((res) => {
				let eventsCopy = [...events];
				const index = events.findIndex((event) => event._id === eventUpdate._id);
				eventsCopy[index] = Object.assign(eventsCopy[index], res.event);
				setEvents(eventsCopy);
				Swal.fire({
					position: 'center',
					icon: 'success',
					title: ` thay i thnh cng s kin ${eventUpdate.name}`,
					showConfirmButton: false,
					timer: 2000,
					target: 'body',
				});
			})
			.catch((err) => {
				Swal.fire({
					position: 'center',
					icon: 'error',
					title: `C li xy ra khi thay i s kin ${eventUpdate.name}! Vui lng th li`,
					showConfirmButton: false,
					timer: 2000,
					target: 'body',
				});
			});
	};

	const handleDeleteEvent = (eventDelete) => {
		Swal.fire({
			title: 'Bn c chc mun xa s kin ny ?',
			text: 'Hnh ng ny khng th quay li, bn c chc khng?',
			icon: 'warning',
			showCancelButton: true,
			confirmButtonColor: '#1976d2',
			cancelButtonColor: '#d32f2f',
			confirmButtonText: 'ng ',
			cancelButtonText: 'Hy',
		}).then((result) => {
			if (result.isConfirmed) {
				deleteEvent(eventDelete._id)
					.then((res) => {
						setEvents(events.filter((event) => event._id !== eventDelete._id));
						Swal.fire({
							position: 'center',
							icon: 'success',
							title: ` xa s kin ${eventDelete.name}`,
							showConfirmButton: false,
							timer: 2000,
							target: 'body',
						});
					})
					.catch((err) => {
						Swal.fire({
							position: 'center',
							icon: 'error',
							title: `C li xy ra khi xa s kin ${eventDelete.name}! Vui lng th li`,
							showConfirmButton: false,
							timer: 2000,
							target: 'body',
						});
					});
			}
		});
	};
	const handleAddVisiter = (eventUpdate, listVisiter) => {
		addVisiters(eventUpdate._id, listVisiter)
			.then((res) => {
				let eventsCopy = [...events];
				const index = events.findIndex((event) => event._id === eventUpdate._id);
				eventsCopy[index] = Object.assign(eventsCopy[index], res.event);
				setEvents(eventsCopy);
				Swal.fire({
					position: 'center',
					icon: 'success',
					title: ` thm thnh cng ${listVisiter.length} ngi vo s kin ${eventUpdate.name}`,
					showConfirmButton: false,
					timer: 2000,
					target: 'body',
				});
			})
			.catch((err) => {
				Swal.fire({
					position: 'center',
					icon: 'error',
					title: `C li xy ra khi thm ngi tham d s kin ${eventUpdate.name}! Vui lng th li`,
					showConfirmButton: false,
					timer: 2000,
					target: 'body',
				});
			});
	};

	return (
		<Box
			component="main"
			sx={{
				backgroundColor: '#f5f5f5',
				flexGrow: 1,
				height: '100vh',
				overflow: 'auto',
			}}
		>
			<Toolbar />
			<Card
				sx={{
					minWidth: 100,
					position: 'relative',
					top: '15px',
					ml: 4,
					mr: 4,
					boxShadow: 3,
					backgroundColor: '#1976d2',
				}}
			>
				<CardContent>
					<Typography variant="h5" component="div" sx={{ color: 'white' }}>
						Qun l s kin
					</Typography>
					<Typography sx={{ fontSize: 14 }} color="white" gutterBottom>
						Tm kim, thay i, thm mi, xa s kin
					</Typography>
				</CardContent>
			</Card>
			<Container maxWidth="xl" sx={{ mb: 4 }}>
				<Grid container spacing={3}>
					<Grid item xs={12} md={12} lg={12}>
						<Paper
							sx={{
								p: 4,
								pt: 3,
								pb: 0,
								display: 'flex',
								flexDirection: 'column',
								height: '100%',
							}}
						>
							<TableContainer sx={{ maxHeight: '100vh' }}>
								<Table stickyHeader aria-label="sticky table">
									<TableHead>
										<TableRow>
											<TableCell colSpan={1} align="left" sx={{ p: 0 }}>
												<Button
													variant="contained"
													color="warning"
													onClick={handleOpenModalAddEvent}
												>
													Thm s kin mi
												</Button>
												{modalAddEvent && (
													<ModalAddEvent
														isOpen={modalAddEvent}
														handleClose={handleCloseModalAddEvent}
														addNewEventState={addNewEventState}
													/>
												)}
											</TableCell>
											<TableCell colSpan={7}>
												<LocalizationProvider
													dateAdapter={AdapterDateFns}
													locale={enLocale}
												>
													<DatePicker
														label="Chn ngy"
														mask="__/__/____"
														value={date}
														onChange={(newValue) => {
															setDate(newValue);
														}}
														renderInput={(params) => (
															<TextField {...params} />
														)}
													/>
												</LocalizationProvider>
											</TableCell>
										</TableRow>
									</TableHead>
									<TableHead>
										<TableRow>
											<TableCell
												align="center"
												style={{
													minWidth: '200px',
													backgroundColor: '#1976d2',
													color: 'white',
												}}
											>
												<b>Tn s kin</b>
											</TableCell>
											<TableCell
												align="center"
												style={{
													minWidth: '200px',
													backgroundColor: '#1976d2',
													color: 'white',
												}}
											>
												<b>a im t chc</b>
											</TableCell>
											<TableCell
												align="center"
												style={{
													minWidth: '100px',
													backgroundColor: '#1976d2',
													color: 'white',
												}}
											>
												<b>Ngy t chc</b>
											</TableCell>
											<TableCell
												align="center"
												style={{
													minWidth: '150px',
													backgroundColor: '#1976d2',
													color: 'white',
												}}
											>
												<b>Loi s kin</b>
											</TableCell>
											<TableCell
												align="center"
												style={{
													minWidth: '100px',
													backgroundColor: '#1976d2',
													color: 'white',
												}}
											>
												<b>M ng k</b>
											</TableCell>
											<TableCell
												align="center"
												style={{
													minWidth: '100px',
													backgroundColor: '#1976d2',
													color: 'white',
												}}
											>
												<b>Kt thc ng k</b>
											</TableCell>
											<TableCell
												align="center"
												style={{
													minWidth: '100px',
													backgroundColor: '#1976d2',
													color: 'white',
												}}
											>
												<b>S lng tham gia</b>
											</TableCell>
											<TableCell
												align="center"
												style={{
													minWidth: '260px',
													backgroundColor: '#1976d2',
													color: 'white',
												}}
											>
												<b>Hnh ng</b>
											</TableCell>
										</TableRow>
									</TableHead>
									<TableBody>
										{eventsFilter
											.sort(
												(eventA, eventB) =>
													new Date(eventB.dateEvent) -
													new Date(eventA.dateEvent),
											)
											.slice(
												page * rowsPerPage,
												page * rowsPerPage + rowsPerPage,
											)
											?.map((event) => {
												return (
													<RowEvent
														event={event}
														users={listUsers}
														key={event._id}
														handleDeleteEvent={handleDeleteEvent}
														handleUpdateEvent={handleUpdateEvent}
														handleAddVisiter={handleAddVisiter}
													/>
												);
											})}
									</TableBody>
								</Table>
							</TableContainer>
							<TablePagination
								rowsPerPageOptions={[10, 25, 100]}
								component="div"
								count={eventsFilter?.length}
								rowsPerPage={rowsPerPage}
								page={page}
								onPageChange={handleChangePage}
								onRowsPerPageChange={handleChangeRowsPerPage}
							/>
						</Paper>
					</Grid>
				</Grid>
			</Container>
			{children}
		</Box>
	);
}

export default EventManagerComponent;
