import { Button, TableCell, TableRow } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import AddIcon from '@mui/icons-material/Add';
import { format } from 'date-fns';
import ModalEditEvent from './ModalEditEvent';
import { useState } from 'react';
import ModalAddVisiter from './ModalAddVisiter';
export default function RowEvent({
	event,
	users,
	handleDeleteEvent,
	handleUpdateEvent,
	handleAddVisiter,
}) {
	const [modalEditEvent, setModalEditEvent] = useState(false);
	const [modalAddVisiter, setModalAddVisiter] = useState(false);
	const handleOpenModalEditEvent = () => setModalEditEvent(true);
	const handleCloseModalEditEvent = () => {
		setModalEditEvent(false);
	};
	const handleOpenModalAddVisiter = () => setModalAddVisiter(true);
	const handleCloseModalAddVisiter = () => {
		setModalAddVisiter(false);
	};
	return (
		<TableRow hover role="checkbox" tabIndex={-1}>
			<TableCell align="left">{event.name}</TableCell>
			<TableCell align="left">{event.location}</TableCell>
			<TableCell align="center">{format(new Date(event.dateEvent), 'dd/MM/yyyy')}</TableCell>
			<TableCell align="center">
				{event.typeEvent === 'public' && 'S kin cng khai'}
				{event.typeEvent === 'private' && 'S kin ring'}
				{event.typeEvent === 'restricted' && 'S kin hn ch'}
			</TableCell>
			<TableCell align="center">
				{event.openReg ? format(new Date(event.openReg), 'dd/MM/yyyy') : ''}
			</TableCell>
			<TableCell align="center">
				{event.endReg ? format(new Date(event.endReg), 'dd/MM/yyyy') : ''}
			</TableCell>
			<TableCell align="center">
				{event.typeEvent !== 'public'
					? event.listVisitersCheckin.filter((el) => el.isCheckin === false)
							.length
					: 'N/A'}
			</TableCell>
			<TableCell align="right">
				{event.typeEvent !== 'public' && (
					<>
						<Button
							variant="contained"
							size="small"
							onClick={handleOpenModalAddVisiter}
						>
							<AddIcon />
						</Button>
						{modalAddVisiter && (
							<ModalAddVisiter
								event={event}
								users={users}
								isOpenAddVisiter={modalAddVisiter}
								handleCloseAddVisiter={handleCloseModalAddVisiter}
								handleAddVisiter={handleAddVisiter}
							/>
						)}
					</>
				)}
				<Button
					variant="contained"
					color="success"
					sx={{ ml: 2 }}
					size="small"
					onClick={handleOpenModalEditEvent}
				>
					<EditIcon />
				</Button>
				{modalEditEvent && (
					<ModalEditEvent
						event={event}
						isOpenEdit={modalEditEvent}
						handleCloseEdit={handleCloseModalEditEvent}
						handleUpdateEvent={handleUpdateEvent}
					/>
				)}
				<Button
					variant="contained"
					color="error"
					sx={{ ml: 2 }}
					size="small"
					onClick={() => {
						handleDeleteEvent(event);
					}}
				>
					<DeleteForeverIcon />
				</Button>
			</TableCell>
		</TableRow>
	);
}
