import { Button, Grid, MenuItem, Modal, TextField, Typography } from '@mui/material';
import { Box } from '@mui/system';
import { useForm } from 'react-hook-form';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DatePicker from '@mui/lab/DatePicker';
import enLocale from 'date-fns/locale/en-US';
import { useState } from 'react';

export default function ModalEditEvent({ event, isOpenEdit, handleCloseEdit, handleUpdateEvent }) {
	const {
		register,
		handleSubmit,
		setValue,
		formState: { errors },
	} = useForm();
	const [dateModal, setDateModal] = useState(event.dateEvent);
	const [typeEvent, setTypeEvent] = useState(event.typeEvent);
	const [dateOpenRegModal, setDateOpenRegModal] = useState(event.openReg);
	const [dateEndRegModal, setDateEndRegModal] = useState(event.endReg);
	const update = (data) => {
		handleCloseEdit();
		handleUpdateEvent(event, data);
	};
	return (
		<Modal
			open={isOpenEdit}
			aria-labelledby="modal-modal-title-edit"
			aria-describedby="modal-modal-description-edit"
		>
			<Box
				sx={{
					position: 'absolute',
					top: '50%',
					left: '50%',
					transform: 'translate(-50%, -50%)',
					width: 600,
					height: 'calc(100vh - 300px)',
					bgcolor: 'white',
					boxShadow: 24,
				}}
			>
				<Box
					sx={{
						bgcolor: 'blue',
						color: 'white',
						p: 3,
					}}
				>
					<Typography
						id="modal-modal-title-edit"
						variant="h5"
						component="h2"
						align="center"
						sx={{ fontWeight: 'bolder' }}
					>
						Thay i s kin
					</Typography>
				</Box>
				<Box
					component="form"
					onSubmit={handleSubmit(update)}
					sx={{
						p: 3,
					}}
				>
					<Grid container spacing={3}>
						<Grid item xs={12} md={12} sm={12} lg={12}>
							<TextField
								fullWidth
								error={errors.name ? true : false}
								helperText={errors.name && 'Vui lng nhp tn s kin'}
								type="text"
								label="Tn s kin*"
								variant="standard"
								defaultValue={event.name}
								{...register('name', { required: true })}
							/>
						</Grid>
						<Grid item xs={12} md={12} sm={12} lg={12}>
							<TextField
								fullWidth
								error={errors.location ? true : false}
								helperText={
									errors.location && 'Vui lng nhp a im t chc s kin'
								}
								type="text"
								label="a im t chc*"
								variant="standard"
								defaultValue={event.location}
								{...register('location', { required: true })}
							/>
						</Grid>
						<Grid item xs={12} md={12} sm={12} lg={12}>
							<LocalizationProvider dateAdapter={AdapterDateFns} locale={enLocale}>
								<DatePicker
									label="Chn ngy*"
									mask="__/__/____"
									value={dateModal}
									onChange={(newDate) => {
										setDateModal(newDate);
										setValue('dateEvent', newDate);
									}}
									renderInput={(params) => <TextField {...params} />}
								/>
							</LocalizationProvider>
						</Grid>
						<Grid item xs={12} md={12} sm={12} lg={12}>
							<TextField
								fullWidth
								error={errors.typeEvent ? true : false}
								helperText={errors.typeEvent && 'Vui lng chn loi s kin'}
								select
								label="Loi s kin*"
								value={typeEvent}
								{...register('typeEvent', { required: true })}
								onChange={(event) => {
									setTypeEvent(event.target.value);
								}}
							>
								<MenuItem value={'public'}>S Kin Cng Khai</MenuItem>
								<MenuItem value={'private'}>S Kin Ring</MenuItem>
								<MenuItem value={'restricted'}>S Kin Hn Ch</MenuItem>
							</TextField>
						</Grid>
						{typeEvent === 'restricted' && (
							<>
								<Grid item xs={12} md={12} sm={12} lg={12}>
									<LocalizationProvider
										dateAdapter={AdapterDateFns}
										locale={enLocale}
									>
										<DatePicker
											label="Chn ngy m ng k*"
											mask="__/__/____"
											value={dateOpenRegModal}
											onChange={(newDate) => {
												setDateOpenRegModal(newDate);
												setValue('openReg', newDate);
											}}
											renderInput={(params) => <TextField {...params} />}
										/>
									</LocalizationProvider>
								</Grid>
								<Grid item xs={12} md={12} sm={12} lg={12}>
									<LocalizationProvider
										dateAdapter={AdapterDateFns}
										locale={enLocale}
									>
										<DatePicker
											label="Chn ngy kt thc ng k*"
											mask="__/__/____"
											value={dateEndRegModal}
											onChange={(newDate) => {
												setDateEndRegModal(newDate);
												setValue('endReg', newDate);
											}}
											renderInput={(params) => <TextField {...params} />}
										/>
									</LocalizationProvider>
								</Grid>
							</>
						)}
					</Grid>
					<Box
						sx={{
							p: 3,
							display: 'flex',
							justifyContent: 'flex-end',
						}}
					>
						<Button variant="contained" color="success" type="submit">
							Thay i
						</Button>

						<Button
							variant="contained"
							color="error"
							onClick={handleCloseEdit}
							sx={{ ml: 3 }}
						>
							Hy
						</Button>
					</Box>
				</Box>
			</Box>
		</Modal>
	);
}
